<template>
	<view class="space">
		<navbar title="个人空间" index="space"></navbar>
		<list-scroll class="list-scroll">
			<list-card :key="index" :item="item" v-for="(item,index) in SpaceList">{{item}}</list-card>
			<!-- <uni-load-more iconType="snow" status="noMore"></uni-load-more> -->
		</list-scroll>
		<view class="push" @click="openPush">
			<image src="../../../static/push.png"></image>
		</view>
		<uni-popup ref="popup" type="top" :maskClick="true">
			<view class="popup-wrap">
				<view class="popup-title">发布</view>
				<view class="popup-close" @click="closePush">
					<uni-icons type="closeempty" size="30"></uni-icons>
				</view>
				<view class="popup-header">
					<view class="header-iconbox" @click="pushHightlight">
						<view class="header-iconbox_icons">
							<image src="../../../static/push_gg.png"></image>
						</view>
						<view class="header-iconbox_title">高光</view>
					</view>
					<view class="header-iconbox" @click="pushOffer">
						<view class="header-iconbox_icons">
							<image src="../../../static/push_xs.png"></image>
						</view>
						<view class="header-iconbox_title">悬赏</view>
					</view>
					<view class="header-iconbox" @click="pushRecruit">
						<view class="header-iconbox_icons">
							<image src="../../../static/push_zm.png"></image>
						</view>
						<view class="header-iconbox_title">招募</view>
					</view>
				</view>

			</view>
		</uni-popup>
	</view>

</template>

<script>
	export default {
		data() {
			return {
				SpaceList: []
			}
		},
		onLoad() {
			this.getSpaceList()
		},
		methods: {
			openPush() {
				this.$refs.popup.open()
			},
			closePush(){
				this.$refs.popup.close()
			},
			pushRecruit(){
				uni.navigateTo({
					url:'../../push-zm/push-zm'
				})
				this.$refs.popup.close()
			},
			pushOffer(){
				uni.navigateTo({
					url:'../../push-xs/push-xs'
				})
				this.$refs.popup.close()
			},
			pushHightlight(){
				uni.navigateTo({
					url:'../../push-gg/push-gg'
				})
				this.$refs.popup.close()
			},
			getSpaceList() {
				const url = '../../static/json/list.json'
				this.$http.get(url).then(res => {
					const {
						data
					} = res
					var arr = []
					for (var i = 0; i < data.length; i++) {
						if (data[i].name === 'Tom') {
							arr.push(data[i])
						}
					}
					this.SpaceList = arr
				})
			}
			
		}
	}
</script>

<style lang="scss">
	@import '/common/css/icons.css';

	.space {

		.list-scroll {
			height: 100%;
		}

		.push {
			position: fixed;
			right: 5px;
			bottom: 70px;
			// z-index: 999;
			height: 50px;
			width: 50px;

			image {
				height: 100%;
				width: 100%;
			}
		}

		.popup-wrap {
			margin: 10px 5px;
			background-color: #fff;
			opacity:0.95;
			// height: 100vh;
			border-radius: 20px;
			.popup-title{
				padding-top: 10px;
				padding-left: 20px;
				// text-align: center;
				font-weight: bold;
				font-size: 20px;
			}
			.popup-close{
				position: fixed;
				right: 15px;
				top: 15px;
			}
			.popup-header{
				display: flex;
				justify-content: space-between;
				.header-iconbox {
					display: flex;
					align-items: center;
					flex-direction: column;
					padding: 20px 30px;
					.header-iconbox_icons {
						height: 45px;
						width: 45px;
						image {
							height: 100%;
							width: 100%;
						}
					}
					.header-iconbox_title {
						margin-top: 10px;
						font-size: 18px;
						color: #666;
					}
				}
			}
		}
	}
</style>
